<template>
  <div>
    <div
      class="fixed h-16 shadow-md z-50 flex justify-between px-20 items-center w-full header-wrapper"
    >
      <!-- <img
        class="inline-block h-12 w-12 rounded-full ring-2 ring-white"
        src="/images/avatar.jpg"
      /> -->
      <ul class="menu-horizontal-wrapper">
        <li :class="isHomeActive ? 'active' : ''">
          <router-link to="/">Home</router-link>
        </li>
        <li :class="isAboutActive ? 'active' : ''">
          <router-link to="/about">About</router-link>
        </li>
        <li :class="isSkillActive ? 'active' : ''">
          <router-link to="/skill">My Skills</router-link>
        </li>
        <li :class="isProjectActive ? 'active' : ''">
          <router-link to="/project">Projects</router-link>
        </li>
        <li :class="isContact ? 'active' : ''">
          <router-link to="/contact">Contact Us</router-link>
        </li>
      </ul>
      <div class="menu-wrapper">
        <div class="dropdown">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="20"
            height="14"
            viewBox="0 0 20 14"
          >
            <g
              fill="none"
              fill-rule="evenodd"
              stroke="#000"
              stroke-linecap="round"
              stroke-linejoin="round"
              stroke-width="2"
              transform="translate(1 1)"
            >
              <path d="M0 6h18M0 0h18M0 12h18" />
            </g>
          </svg>
          <div>
            <div class="dropdown-content">
              <router-link to="/">Home</router-link>
              <router-link to="/about">About</router-link>
              <router-link to="/skill">My Skills</router-link>
              <router-link to="/project">Projects</router-link>
              <router-link to="/contact">Contact Us</router-link>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="content-wrapper">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  name: "DefaultLayout",
  components: {},
  data() {
    return {
      isAboutActive: false,
      isHomeActive: false,
      isSkillActive: false,
      isProjectActive: false,
      isContact: false
    };
  },
  created() {
    const { path } = this.$route;
    if (path) {
      switch (path) {
        case "/":
          this.isHomeActive = true;
          this.isAboutActive = false;
          this.isSkillActive = false;
          this.isProjectActive = false;
          this.isContact = false;
          break;
        case "/about":
          this.isHomeActive = false;
          this.isAboutActive = true;
          this.isSkillActive = false;
          this.isProjectActive = false;
          this.isContact = false;
          break;
        case "/skill":
          this.isHomeActive = false;
          this.isAboutActive = false;
          this.isSkillActive = true;
          this.isProjectActive = false;
          this.isContact = false;
          break;
        case "/project":
          this.isHomeActive = false;
          this.isAboutActive = false;
          this.isSkillActive = false;
          this.isProjectActive = true;
          this.isContact = false;
          break;
        case "/contact":
          this.isHomeActive = false;
          this.isAboutActive = false;
          this.isSkillActive = false;
          this.isProjectActive = false;
          this.isContact = true;
          break;
      }
    }
  },
  updated() {
    const { path } = this.$route;
    if (path) {
      switch (path) {
        case "/":
          this.isHomeActive = true;
          this.isAboutActive = false;
          this.isSkillActive = false;
          this.isProjectActive = false;
          this.isContact = false;
          break;
        case "/about":
          this.isHomeActive = false;
          this.isAboutActive = true;
          this.isSkillActive = false;
          this.isProjectActive = false;
          this.isContact = false;
          break;
        case "/skill":
          this.isHomeActive = false;
          this.isAboutActive = false;
          this.isSkillActive = true;
          this.isProjectActive = false;
          this.isContact = false;
          break;
        case "/project":
          this.isHomeActive = false;
          this.isAboutActive = false;
          this.isSkillActive = false;
          this.isProjectActive = true;
          this.isContact = false;
          break;
        case "/contact":
          this.isHomeActive = false;
          this.isAboutActive = false;
          this.isSkillActive = false;
          this.isProjectActive = false;
          this.isContact = true;
          break;
      }
    }
  },
  mounted() {},

  methods: {},
};
</script>

<style lang="css" scoped>
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

li {
  float: left;
  font-size: 20px;
}

li a {
  display: block;
  text-align: center;
  padding: 16px 16px;
  text-decoration: none;
}

li a:hover {
  color: #fc6b03;
}

li.active::after {
  content: "";
  display: block;
  border-bottom: 2px solid #fc6b03;
}

.header-wrapper {
  background-color: white;
}

.content-wrapper {
  position: absolute;
  width: 100%;
  top: 64px;
}

@media only screen and (max-width: 960px) {
  .menu-horizontal-wrapper {
    visibility: hidden;
  }

  .menu-wrapper {
    visibility: visible;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f1f1f1;
    max-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 2;
  }

  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }

  .dropdown-content a:hover {
    background-color: #ddd;
  }

  .dropdown:hover .dropdown-content {
    display: block;
  }
}

@media only screen and (min-width: 960px) {
  .menu-horizontal-wrapper {
    width: 100%;
    visibility: visible;
  }

  .menu-wrapper {
    visibility: hidden;
  }
}
</style>
